import React,{Component} from 'react'
import firebase from './firebase'
import {Redirect} from 'react-router-dom'
import logo from './locus logo.jpg'

class Login extends Component
{
  constructor()
  {
    super()
    this.state = {username : "",
                  password : "",isLogged : false,isError :false,
                  }

    this.handleUserChange = this.handleUserChange.bind(this) 
    this.handlePassChange = this.handlePassChange.bind(this) 
    this.handleSubmit = this.handleSubmit.bind(this)            
  }
   handleUserChange(evt) {
    this.setState({
      username: evt.target.value,
    });
  };

  handlePassChange(evt) {
    this.setState({
      password: evt.target.value,
    });
  }
  handleSubmit()
  {
    if (this.state.username === null || this.state.password === null)
      {this.setState({isError : true })}
    else 
    {
      const LoginRef = firebase.database().ref('login').child(this.state.username)
      // LoginRef.update()
      LoginRef.on('value',snapshot => {
        const data = snapshot.val()
        if(this.state.password === data.password)
          {
            this.setState({isLogged : true})
           }
      })
      
    }

  }

  render()
  {
      // firebase.database().ref('login').child(i)

    return(
            <div className = 'log'>
            <br/>
            <h7>KEAM MOCK TEST</h7>
            <div class="login-page">
            <div class="form">
            <img src ={logo} alt =''/>
            <br/>
            <br/>
            <form>
            <input type="text" placeholder="&#xf007;  username" value={this.state.username} onChange={this.handleUserChange}/>
            <input type="password" placeholder="&#xf023; password" value={this.state.password} onChange={this.handlePassChange}/>
            <button onClick = {this.handleSubmit}>LOGIN</button>
            <p class="message"></p>
            </form>
            </div>
            </div>
            {this.state.isLogged ? <Redirect to ={{pathname:"/Post" , state :{username:this.state.username}  }}/>:<Redirect to ='/'/>}
            </div>
)
  }
}

export default Login
